<template>
  <div class="product-wrapper">
    <div class="row product-text-wrapper">
      <h2 class="w-100">{{product.text.h2}}</h2>
      <h3 class="w-100">{{product.text.h3}}</h3>
      <p class="w-100" v-if="product.text.dollar[0]!=undefined">
        <span>
          From
          <span>{{product.text.dollar[0]}}/mo.</span> or&nbsp;
          <span>{{product.text.dollar[1]}}</span>&nbsp;with&nbsp;trade‑in.
        </span>
        <sup>
          <a href="#footnote-1">1</a>
        </sup>
      </p>
      <div class="w-100">
        <a class="LearnMore" href="/iphone-se/">Learn more</a>
        <a class="Buy" href="/us/shop/goto/buy_iphone/iphone_se">Buy</a>
      </div>
    </div>
    <div class="row product-image-wrapper">
      <figure class="ProductImage" :style="$attrs.iphoneSE"></figure>
    </div>
  </div>
</template>
<script>
export default {
  inheritAttrs: false,
  props: {
    product: {
      type: Object,
      required: true,
    },
  },
  mounted() {
    let scopeImages = this.product;
    let el = this.$el;
    function imageResponse() {
      if (el.clientWidth < 734) {
        el.querySelector(
          ".ProductImage"
        ).style.backgroundImage = `url(${scopeImages.sm})`;
        console.log("sm");
      } else if (el.clientWidth < 1069) {
        el.querySelector(
          ".ProductImage"
        ).style.backgroundImage = `url(${scopeImages.md})`;
        console.log("md");
      } else if (el.clientWidth >= 1069) {
        el.querySelector(
          ".ProductImage"
        ).style.backgroundImage = `url(${scopeImages.lg})`;
        console.log("lg");
      }
    }
    imageResponse();
    window.matchMedia("(min-width: 734px)").addListener(imageResponse);
    window.matchMedia("(min-width: 1069px)").addListener(imageResponse);
  },
};
</script>
<style scoped>
.product-wrapper {
  margin: 0 auto;
  text-align: center;
  height: 800px;
  background-color: inherit;
  position: relative;
}
.product-text-wrapper {
  width: 100%;
  position: absolute;
  top: 50px;
  z-index: 1;
}
.product-image-wrapper {
  width: 100%;
  height: 80%;
  position: absolute;
  bottom: 0;
}
.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.w-100 {
  width: 100%;
}

.Buy {
  margin-left: 32px;
}
h2 {
  margin: 0 auto;
  font-size: 1em;
}

h3 {
  margin: 6px auto 0 auto;
  font-size: 0.5em;
}
p {
  margin: 6px auto 0 auto;
  color: #86868b;
  font-size: 0.5em;
}
a {
  color: #2997ff;
  text-decoration: none;
  font-size: 0.5em;
}
a:hover {
  color: #2997ff;
  text-decoration: none;
}
figure {
  margin: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
}
@media only screen and (min-width: 1069px) {
  .product-wrapper {
    font-size: 56px;
    padding-top: 55px;
  }
}
@media only screen and (max-width: 1068px) and (min-width: 735px) {
  .product-wrapper {
    font-size: 48px;
    padding-top: 37px;
  }
}
@media only screen and (max-width: 734px) {
  .product-wrapper {
    font-size: 32px;
    padding-top: 37px;
  }
}
</style>

